.App {
  font-family: sans-serif;
  text-align: center;
}
#game {
  text-decoration: underline;
}

#gameTitle {
  font-size: 35px;
  text-align: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  padding: 10px;
  border: 5px solid black;
  background-color: gainsboro;
}

#player1 {
  position: relative;
  float: left;
  width: 400px;
  margin: 3px;
  border: 2px solid rebeccapurple;
  text-align: center;
  font-family: "Audiowide", cursive;
  background-color: powderblue;
}

#player2 {
  position: relative;
  float: left;
  width: 400px;
  margin: 3px;
  border: 2px solid yellowgreen;
  text-align: center;
  font-family: "Audiowide", cursive;
  background-color: powderblue;
}

#shootBtn {
  background-color: lightgreen;
  border: none;
  color: white;
  text-align: center;
  font-family: "Audiowide", cursive;
  font-size: 20px;
  cursor: pointer;
  padding: 10px 20px;
}
#shootBtn:hover {
  background-color: Green;
  padding: 10px 32px;
}
#restart {
  background-color: rgb(226, 209, 112);
  border: none;
  color: white;
  text-align: center;
  font-family: "Audiowide", cursive;
  font-size: 20px;
  cursor: pointer;
  padding: 10px 20px;
}
#restart:hover {
  background-color: gold;
  padding: 10px 32px;
}

.Score-Board {
  border: 1px solid black;
  background-color: black;
  padding: 5px;
  display: grid;
  grid-template-columns: 35% 35% auto;
}

.item {
  background-color: wheat;
  border: 1px solid black;
  padding: 10px;
  font-size: 20px;
  text-align: center;
  font-weight: bold;
}

.result {
  border: 1px solid black;
  background-color: black;
  padding: 2px;
  display: grid;
  font-weight: bold;
  color: white;
}

#p1 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
}
#p2 {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
}

@media (max-width: 800px) {
  #player1 {
    position: relative;
    float: left;
    width: 90px;
    border: 2px solid rebeccapurple;
    margin: 2px;
    text-align: center;
    font-family: "Audiowide", cursive;
    background-color: powderblue;
  }

  #player2 {
    position: relative;
    float: left;
    width: 90px;
    border: 2px solid yellowgreen;
    margin: 2px;
    text-align: center;
    font-family: "Audiowide", cursive;
    background-color: powderblue;
  }
  #h1 {
    margin: 0;
    display: flex;
    justify-content: center;
    align-items: left;
  }
  #p1 {
    font-size: 12px;
    font-weight: bold;
  }
  #p2 {
    font-size: 12px;
    font-weight: bold;
  }
}
